html,
body,
#root {
  /* text-align: center; */
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", Roboto,
    "Helvetica Neue", Arial, sans-serif, "Apple Color Emoji", "Segoe UI Emoji",
    "Segoe UI Symbol";
  background: rgb(245, 245, 250);
  margin: 0;
}

.homePage {
  background-image: url(./images/frozen-lake-pattern.jpg);
  height: 100%;
  width: 100%;
  overflow: hidden;
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  margin: 0;
}

.homePageTxt {
  display: flex;
  flex-direction: column;
  background: radial-gradient(
    circle,
    rgba(111, 106, 102, 0.6) 0%,
    rgba(111, 106, 102, 0.35) 30%,
    rgba(111, 106, 102, 0.1) 75%
  );
  border-radius: 10px;
  margin: 1% auto 2%;
}

.homePage h1 {
  color: rgb(245, 245, 255);
  width: fit-content;
  align-self: center;
  text-shadow: 2px 2px rgb(13, 33, 33);
}

.homePage p {
  width: fit-content;
  align-self: center;
  text-shadow: 1px 1px rgb(13, 33, 33);
  color: rgb(245, 245, 255);
  font-weight: 600;
  font-size: 1.1em;
}

.homepageBtns {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
}

.homepageBtns p {
  margin: 4%;
  font-weight: 500;
  font-style: italic;
}

.homepageBtns a {
  background-color: rgba(111, 106, 102, 0.5);
  color: white;
  text-decoration: none;
  border: 2px solid rgb(245, 245, 255);
  border-radius: 10px;
  padding: 5px 10px;
  text-align: center;
  display: inline-block;
  transition: 0.4s;
}

.homepageBtns a:hover {
  background-color: rgb(245, 245, 255, 0.9);
  color: rgb(13, 33, 33);
  text-shadow: none;
}

.homepageBtns a:active {
  position: relative;
  top: 1px;
}

h1 {
  display: flex;
  justify-content: center;
  font-size: 2em;
  color: rgb(10, 10, 10);
  margin: 2%;
}

nav {
  background-color: rgb(0, 0, 0, 0.9);
  padding: 0.5%;
  overflow: hidden;
}

nav img {
  max-height: 1em;
}

nav .navList {
  grid-area: "navList";
  display: flex;
  margin: 0;
  padding: 0;
  justify-content: space-around;
  list-style-type: none;
}

.navText a {
  color: rgb(245, 245, 250);
  text-decoration: none;
}

.navText a:after {
  content: "";
  display: block;
  height: 1px;
  background: rgb(245, 245, 250);
  transition: width 0.3s ease 0s, left 0.3s ease 0s;
  width: 0;
}

.navText a:hover:after {
  width: 100%;
}

h3 {
  margin: 1%;
}

.meInfo {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}

.introText {
  margin: 2% 2%;
}

.exp {
  padding: 2% 4%;
  margin: 2%;
}

.projectPane {
  display: flex;
  flex-direction: column;
  align-items: center;
}

.projectBox {
  background-color: white;
  color: rgb(46, 20, 9);
  border: 2px double rgba(123, 126, 141, 1);
  border-radius: 5px;
  width: 90%;
  margin: 2% 0;
  box-shadow: 0 0 5px black;
}

.proj-name {
  display: flex;
  justify-content: center;
  align-items: center;
  border-bottom: 1px dashed rgba(148, 182, 210, 1);
  padding: 1%;
  margin: 0;
  font-weight: bold;
}

.proj-desc {
  display: flex;
  justify-content: center;
  align-items: center;
  font-size: 0.9em;
  margin: 0;
  padding: 1%;
  min-height: 65%;
}

.proj-links {
  display: flex;
  justify-content: space-evenly;
  border-top: 1px dashed rgba(148, 182, 210, 1);
  padding: 0.5% 0 1%;
}

.proj-links a {
  color: rgb(46, 20, 9);
  font-size: 0.9em;
  margin: 0 1%;
  text-decoration: none;
  font-weight: bolds;
}

.proj-links a:hover {
  text-decoration: underline;
}

form {
  display: flex;
  flex-direction: column;
  align-items: center;
  margin: 0 5%;

  min-height: 70vh;
}

.inputContainer {
  width: 40vw;
}

.message-container {
  width: 60vw;
  height: 40vh;
  margin-bottom: 5%;
}

textarea {
  resize: none;
  width: 100%;
  height: 100%;
  font-family: inherit;
}

input {
  padding: 1%;
  margin: 5%;
  border-radius: 10px;
}

.submitBtn {
  cursor: pointer;
  border: 1px solid darkslategrey;
  background: linear-gradient(whitesmoke, rgb(190, 190, 200));
  font-weight: 500;
  padding: 0.5% 4%;
  margin: 8%;
}

.submitBtn:active {
  position: relative;
  top: 1px;
  background: linear-gradient(rgb(190, 190, 200), whitesmoke);
}

.response {
  font-size: 1.2em;
  margin: 1%;
}

#message {
  display: inline-block;
}

.mePicture {
  width: 50vw;
  height: 50vh;
  object-fit: cover;
  border-radius: 10px;
  margin: 1%;
}

@media screen and (min-width: 768px) {
  h1 {
    font-size: 3em;
    margin: 1%;
  }

  .homepageBtns {
    flex-wrap: nowrap;
  }

  nav {
    min-height: auto;
  }

  .introText {
    margin: 1% 2% 1%;
  }

  .mePicture {
    object-fit: cover;
  }

  .exp {
    position: relative;
    top: 30%;
  }

  .projectPane {
    display: grid;
    grid-template: repeat(2, 1fr) / repeat(2, 1fr);
    justify-items: center;
  }
  .message-container {
    margin-bottom: 3%;
  }

  textarea {
    font-size: 1em;
    margin: 1%;
  }

  input {
    padding: 0.5%;
    margin: 1%;
    font-size: 1.1em;
  }

  .submitBtn {
    margin: 2%;
  }
}
